/* ============================================
   GEMINI ASK - Estilos y Animaciones
   Tema: Paz, Calma y Recursos Humanos
   Colores: Azules suaves, verdes menta, lavanda
   ============================================ */

/* Keyframes para animaciones */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeInScale {
    from {
        opacity: 0;
        transform: scale(0.95);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes slideInLeft {
    from {
        opacity: 0;
        transform: translateX(-20px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes shimmer {
    0% {
        background-position: -200% 0;
    }
    100% {
        background-position: 200% 0;
    }
}

@keyframes gentlePulse {
    0%, 100% {
        box-shadow: 0 4px 20px rgba(96, 165, 250, 0.2);
    }
    50% {
        box-shadow: 0 4px 30px rgba(96, 165, 250, 0.35);
    }
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-5px);
    }
}

@keyframes breathe {
    0%, 100% {
        opacity: 0.4;
    }
    50% {
        opacity: 0.7;
    }
}

/* ============================================
   Modal Principal - Tema Claro y Pacfico
   ============================================ */
.gemini-modal .ant-modal-content {
    border-radius: 24px;
    overflow: hidden;
    background: linear-gradient(165deg, #ffffff 0%, #f0f9ff 40%, #f5f3ff 100%);
    border: 1px solid rgba(96, 165, 250, 0.15);
    box-shadow: 
        0 25px 60px -15px rgba(96, 165, 250, 0.2),
        0 10px 30px -10px rgba(167, 139, 250, 0.1),
        0 0 0 1px rgba(255, 255, 255, 0.8);
}

.gemini-modal .ant-modal-header {
    background: linear-gradient(135deg, rgba(240, 249, 255, 0.9) 0%, rgba(245, 243, 255, 0.9) 100%);
    border-bottom: 1px solid rgba(96, 165, 250, 0.1);
    padding: 24px 28px;
}

.gemini-modal .ant-modal-body {
    padding: 0;
    max-height: 70vh;
    overflow-y: auto;
    background: transparent;
}

.gemini-modal .ant-modal-close {
    color: #94a3b8;
    transition: all 0.3s ease;
}

.gemini-modal .ant-modal-close:hover {
    color: #64748b;
    transform: rotate(90deg);
}

/* Scrollbar suave */
.gemini-modal .ant-modal-body::-webkit-scrollbar {
    width: 6px;
}

.gemini-modal .ant-modal-body::-webkit-scrollbar-track {
    background: rgba(148, 163, 184, 0.1);
    border-radius: 3px;
}

.gemini-modal .ant-modal-body::-webkit-scrollbar-thumb {
    background: linear-gradient(180deg, #93c5fd, #c4b5fd);
    border-radius: 3px;
}

/* ============================================
   Header del Modal
   ============================================ */
.gemini-modal-header {
    display: flex;
    align-items: center;
    gap: 16px;
}

.gemini-avatar {
    width: 52px;
    height: 52px;
    border-radius: 16px;
    background: linear-gradient(135deg, #60a5fa 0%, #a78bfa 50%, #f0abfc 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    animation: float 4s ease-in-out infinite;
    box-shadow: 
        0 8px 25px rgba(96, 165, 250, 0.3),
        0 4px 10px rgba(167, 139, 250, 0.2);
}

.gemini-avatar-icon {
    font-size: 26px;
}

.gemini-header-text {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.gemini-greeting {
    font-size: 20px;
    font-weight: 700;
    color: #1e40af;
    letter-spacing: -0.3px;
}

.gemini-subtitle {
    font-size: 13px;
    color: #64748b;
    font-weight: 400;
}

/* ============================================
   Contenedor del Contenido
   ============================================ */
.gemini-content-wrapper {
    position: relative;
    padding: 28px;
    opacity: 0;
    transform: translateY(20px);
    transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.gemini-content-wrapper.visible {
    opacity: 1;
    transform: translateY(0);
}

.gemini-sparkle-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: 
        radial-gradient(circle at 15% 25%, rgba(96, 165, 250, 0.08) 0%, transparent 50%),
        radial-gradient(circle at 85% 75%, rgba(167, 139, 250, 0.08) 0%, transparent 50%),
        radial-gradient(circle at 50% 50%, rgba(52, 211, 153, 0.05) 0%, transparent 60%);
    pointer-events: none;
    animation: breathe 6s ease-in-out infinite;
}

.gemini-response-content {
    position: relative;
    z-index: 1;
    color: #334155 !important;
}

/* Asegurar que todo el texto tenga color visible (override inline styles) */
.gemini-response-content div,
.gemini-response-content span,
.gemini-response-content p,
.gemini-response-content li,
.gemini-response-content td,
.gemini-response-content th,
.gemini-response-content a {
    color: #334155 !important;
}

.gemini-response-content h1,
.gemini-response-content h2,
.gemini-response-content h3,
.gemini-response-content h4,
.gemini-response-content h5,
.gemini-response-content h6,
.gemini-response-content strong,
.gemini-response-content b {
    color: #1e40af !important;
}

/* ============================================
   Ttulo de Card
   ============================================ */
.gemini-card-title {
    font-size: 18px;
    font-weight: 700;
    color: #1e40af !important;
    margin-bottom: 16px;
    padding-bottom: 12px;
    border-bottom: 2px solid rgba(96, 165, 250, 0.2);
}

.gemini-card-title h3 {
    margin: 0;
    font-size: inherit;
    font-weight: inherit;
    color: inherit !important;
}

/* ============================================
   Secciones (Descripcin, Objetivo, Metodologa)
   ============================================ */
.gemini-section {
    margin-bottom: 20px;
}

.gemini-section:last-child {
    margin-bottom: 0;
}

.gemini-section-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
}

.gemini-section-icon {
    font-size: 18px;
}

.gemini-section-title {
    font-size: 14px;
    font-weight: 600;
    color: #3730a3 !important;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.gemini-section-content {
    padding-left: 26px;
}

.gemini-section-content p {
    margin: 0 0 8px 0;
    line-height: 1.7;
}

.gemini-section-content p:last-child {
    margin-bottom: 0;
}

/* Estilos especficos para listas en secciones */
.gemini-section-content ol,
.gemini-section-content ul {
    margin: 0;
    padding-left: 20px;
    list-style: none;
    counter-reset: plan-counter;
}

.gemini-section-content ol li,
.gemini-section-content ul li {
    position: relative;
    padding: 12px 12px 12px 45px;
    margin-bottom: 10px;
    background: linear-gradient(135deg, rgba(240, 249, 255, 0.8) 0%, rgba(245, 243, 255, 0.6) 100%);
    border-radius: 10px;
    border-left: 3px solid #60a5fa;
    color: #334155 !important;
    font-size: 14px;
    line-height: 1.6;
    counter-increment: plan-counter;
    opacity: 1 !important;
    animation: none !important;
}

.gemini-section-content ol li::before {
    content: counter(plan-counter);
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    width: 24px;
    height: 24px;
    background: linear-gradient(135deg, #60a5fa 0%, #818cf8 100%);
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 700;
    color: white !important;
}

.gemini-section-content ul li::before {
    content: '';
    position: absolute;
    left: 12px;
    top: 14px;
    color: #60a5fa !important;
    font-size: 10px;
}

/* ============================================
   Tarjetas de Contenido (gemini-card)
   ============================================ */
.gemini-response-content .gemini-card,
.gemini-response-content .card {
    background: linear-gradient(145deg, #ffffff 0%, #f8fafc 100%);
    border: 1px solid rgba(96, 165, 250, 0.15);
    border-radius: 20px;
    padding: 24px 28px;
    margin-bottom: 20px;
    box-shadow: 
        0 4px 20px rgba(96, 165, 250, 0.08),
        0 2px 8px rgba(0, 0, 0, 0.03);
    animation: fadeInUp 0.6s ease-out forwards;
    opacity: 0;
    transition: all 0.35s ease;
    position: relative;
    overflow: hidden;
}

.gemini-response-content .gemini-card::before,
.gemini-response-content .card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, #60a5fa, #a78bfa, #34d399);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.gemini-response-content .gemini-card:hover::before,
.gemini-response-content .card:hover::before {
    opacity: 1;
}

.gemini-response-content .gemini-card:hover,
.gemini-response-content .card:hover {
    transform: translateY(-4px);
    border-color: rgba(96, 165, 250, 0.25);
    box-shadow: 
        0 12px 35px rgba(96, 165, 250, 0.15),
        0 4px 15px rgba(167, 139, 250, 0.08);
}

/* Delay escalonado para las cards */
.gemini-response-content .gemini-card:nth-child(1),
.gemini-response-content .card:nth-child(1) { animation-delay: 0.1s; }
.gemini-response-content .gemini-card:nth-child(2),
.gemini-response-content .card:nth-child(2) { animation-delay: 0.2s; }
.gemini-response-content .gemini-card:nth-child(3),
.gemini-response-content .card:nth-child(3) { animation-delay: 0.3s; }
.gemini-response-content .gemini-card:nth-child(4),
.gemini-response-content .card:nth-child(4) { animation-delay: 0.4s; }
.gemini-response-content .gemini-card:nth-child(5),
.gemini-response-content .card:nth-child(5) { animation-delay: 0.5s; }
.gemini-response-content .gemini-card:nth-child(n+6),
.gemini-response-content .card:nth-child(n+6) { animation-delay: 0.6s; }

/* ============================================
   Ttulos (gemini-title)
   ============================================ */
.gemini-response-content .gemini-title,
.gemini-response-content b {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    font-size: 15px;
    font-weight: 700;
    color: #1e40af;
    margin-bottom: 8px;
    letter-spacing: 0.2px;
    position: relative;
}

.gemini-response-content .gemini-title::before {
    content: '';
    font-size: 14px;
}

/* ============================================
   Listas de Accin (gemini-action-list)
   ============================================ */
.gemini-response-content .gemini-action-list,
.gemini-response-content ol {
    list-style: none;
    padding: 0;
    margin: 16px 0 0 0;
    counter-reset: step-counter;
}

.gemini-response-content .gemini-action-list li,
.gemini-response-content ol li {
    position: relative;
    padding: 18px 20px 18px 65px;
    margin-bottom: 14px;
    background: linear-gradient(135deg, rgba(240, 249, 255, 0.8) 0%, rgba(245, 243, 255, 0.6) 100%);
    border-radius: 14px;
    border-left: 4px solid #60a5fa;
    color: #334155;
    font-size: 14px;
    line-height: 1.7;
    counter-increment: step-counter;
    animation: slideInLeft 0.5s ease-out forwards;
    opacity: 0;
    transition: all 0.3s ease;
    box-shadow: 0 2px 8px rgba(96, 165, 250, 0.06);
}

.gemini-response-content .gemini-action-list li::before,
.gemini-response-content ol li::before {
    content: counter(step-counter);
    position: absolute;
    left: 18px;
    top: 50%;
    transform: translateY(-50%);
    width: 32px;
    height: 32px;
    background: linear-gradient(135deg, #60a5fa 0%, #818cf8 100%);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    font-weight: 700;
    color: white;
    box-shadow: 0 4px 12px rgba(96, 165, 250, 0.3);
}

.gemini-response-content .gemini-action-list li:hover,
.gemini-response-content ol li:hover {
    background: linear-gradient(135deg, rgba(224, 242, 254, 0.9) 0%, rgba(237, 233, 254, 0.7) 100%);
    transform: translateX(6px);
    border-left-color: #3b82f6;
    box-shadow: 0 6px 20px rgba(96, 165, 250, 0.12);
}

/* Delay escalonado para los items de la lista */
.gemini-response-content .gemini-action-list li:nth-child(1),
.gemini-response-content ol li:nth-child(1) { animation-delay: 0.3s; }
.gemini-response-content .gemini-action-list li:nth-child(2),
.gemini-response-content ol li:nth-child(2) { animation-delay: 0.4s; }
.gemini-response-content .gemini-action-list li:nth-child(3),
.gemini-response-content ol li:nth-child(3) { animation-delay: 0.5s; }
.gemini-response-content .gemini-action-list li:nth-child(4),
.gemini-response-content ol li:nth-child(4) { animation-delay: 0.6s; }
.gemini-response-content .gemini-action-list li:nth-child(5),
.gemini-response-content ol li:nth-child(5) { animation-delay: 0.7s; }
.gemini-response-content .gemini-action-list li:nth-child(n+6),
.gemini-response-content ol li:nth-child(n+6) { animation-delay: 0.8s; }

/* ============================================
   Listas no ordenadas
   ============================================ */
.gemini-response-content ul {
    list-style: none;
    padding: 0;
    margin: 12px 0;
}

.gemini-response-content ul li {
    position: relative;
    padding: 10px 10px 10px 30px;
    margin-bottom: 8px;
    color: #475569;
    font-size: 14px;
    line-height: 1.6;
    animation: fadeInUp 0.4s ease-out forwards;
    opacity: 0;
}

.gemini-response-content ul li::before {
    content: '';
    position: absolute;
    left: 8px;
    color: #60a5fa;
    font-size: 10px;
}

/* ============================================
   Prrafos y texto general
   ============================================ */
.gemini-response-content p {
    color: #475569;
    font-size: 14px;
    line-height: 1.8;
    margin-bottom: 12px;
}

.gemini-response-content strong {
    color: #1e40af;
    font-weight: 600;
}

.gemini-response-content em {
    color: #7c3aed;
    font-style: italic;
}

/* ============================================
   Headers dentro del contenido
   ============================================ */
.gemini-response-content h1,
.gemini-response-content h2,
.gemini-response-content h3,
.gemini-response-content h4 {
    color: #1e3a5f;
    margin: 24px 0 16px 0;
    font-weight: 700;
}

.gemini-response-content h1 { font-size: 24px; color: #1e40af; }
.gemini-response-content h2 { font-size: 20px; color: #1e40af; }
.gemini-response-content h3 { font-size: 17px; color: #3730a3; }
.gemini-response-content h4 { font-size: 15px; color: #4338ca; }

/* ============================================
   Popover Styles
   ============================================ */
.gemini-popover-title {
    font-weight: 600;
    font-size: 14px;
    color: #1e40af;
}

.gemini-popover-content {
    font-size: 13px;
    color: #64748b;
    max-width: 220px;
    display: block;
    line-height: 1.5;
}

/* ============================================
   Responsive
   ============================================ */
@media (max-width: 768px) {
    .gemini-modal .ant-modal-content {
        margin: 10px;
        border-radius: 20px;
    }
    
    .gemini-content-wrapper {
        padding: 20px;
    }
    
    .gemini-response-content .gemini-card,
    .gemini-response-content .card {
        padding: 18px 20px;
        border-radius: 16px;
    }
    
    .gemini-response-content .gemini-action-list li,
    .gemini-response-content ol li {
        padding: 14px 14px 14px 55px;
        font-size: 13px;
    }
    
    .gemini-response-content .gemini-action-list li::before,
    .gemini-response-content ol li::before {
        width: 28px;
        height: 28px;
        font-size: 12px;
        left: 14px;
    }
    
    .gemini-avatar {
        width: 44px;
        height: 44px;
    }
    
    .gemini-avatar-icon {
        font-size: 22px;
    }
    
    .gemini-greeting {
        font-size: 17px;
    }
    
    .gemini-subtitle {
        font-size: 12px;
    }
}

