@import '../../styles/helpers';

.pageContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.errorContainer {
  margin: 40px auto;
  width: 100%;
  text-align: center;
  @media (max-width: breakpoint-max(md))
  and (min-device-width: 320px)
  and (max-device-width: 480px) {
    margin-top: 30%;

  }
}

.errorCode {
  font-size: 72px;
  font-weight: $font-weight-bold;
  line-height: 1;
  color: $violet;
}

.errorInfo {
  margin: 16px auto 32px;
  font-size: 20px;
  font-weight: $font-weight-semi-bold;
  color: $base-dark-gray;
}

.errorHelp {
  margin-bottom: 32px;
  font-size: 1rem;
  font-weight: $font-weight-semi-bold;
  color: $base-middle-gray;
}

.errorBtn {
  font-size: $font-size-sm;
  font-weight: $font-weight-semi-bold;
}

.imageContainer {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 80%;
  padding: 10px 0 10px;
  background-color: $white;

}

.errorImage {
  width: 60%;
  display: block;
  margin-left: auto;
  margin-right: auto;
 

  @media (max-width: breakpoint-max(md)) {
    width: 296px;
    height: 176px;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: $layout-padding;
  background-color: $white;
  color: $base-dark-gray;
  font-size: $font-size-sm;
  margin-bottom: 20%;
}

.footerLabel {
  margin-right: 4px;
}
